
/**
 * 
 * @param {String} tag 
 * @param {Object} attrs 
 * @param  {String | Object} children 
 */
function createElement(tag,attrs,...children) {
    return {
        tag,
        attrs,
        children
    }
}

class Component {
    constructor(props = {}) {
        this.props = props
        this.state = {}
    }
}



export default {
    createElement,
    Component
}